.b-day-time-block {
    width: 25%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    color: #f92927;
    font-weight: bold;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.b-day-time-block__val {
    font-size: 175px;
}

.b-day-time-block__label {
    font-size: 30px;
}

@media only screen and (max-width: 768px) {
    .b-day-time-block {
        width: 100%;
    }

    .b-day-time-block__val {
        font-size: 120px;
    }
}


